.card {
  border-radius: 4px;
  background: transparent;
  width: 100%;
  margin-bottom: 20px;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;

  &:last-of-type {
    margin-bottom: 0;
  }
}

.previewImg {
  width: 100%;
  height: 180px;
  object-fit: cover;
  aspect-ratio: 1 / 2;
  width: 100%;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
}

.innerCard {
  border: 1px solid #b3b3b3;
  background: transparent;
  border-radius: 16px;
  width: 100%;
  min-height: 180px;
  padding: 15px 15px;
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.innerCardWithImage {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-top: none;
}

.expand {
  svg {
    height: 20px;
    width: auto;
  }
  .iconButton {
    transition: all 0.3s ease;
    padding: 7px 13px;
    border-radius: 12px;
  }
  .iconButton:hover {
    background: #f1f1f1;
  }
  // position: absolute;
  // right: 8px;
  // top: 8px;
  display: flex;
  gap: 16px;
  width: calc(100% - 18px);
  align-items: center;
  justify-content: space-between;
  // border-top: 1px solid #2b2b2b57;
  margin-top: 15px;
  padding-top: 10px;
}

.username {
  color: #00000063;
  cursor: pointer;
  transition: color .2s;

  &:hover {
    color: #3b3b3b;
  }
}

.hint {
  font-size: 16px;
  color: #969696;
  font-weight: 300;
}

.icon {
  &:hover {
    color: #3b3b3b;
    fill: #3b3b3b;
  }

  transition: all .2s!important;
  cursor: pointer;
}

.selectedQuestion .innerCardWithImage, .selectedQuestion .innerCard {
  border-width: 2px;
  margin-bottom: 30px;

  &::after {
    content: "";
    color: #bebebe;
    font-size: 6px;
    position: absolute;
    bottom: -25px;
    right: 50%;
    transform: translateX(-50%);
  }
}

.badge {
  color: #3b3b3b;
  text-align: left;
  width: 100%;
  font-size: 16px;
  font-weight: 400;
  font-family: IBM Plex Sans, sans-serif;

  span {
    background: #bebebe;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    box-sizing: border-box;
    display: block;
    text-align: center;
    width: 87px;
    padding-bottom: 7px;
    padding-top: 5px;
    margin-bottom: -3px;
    font-family: IBM Plex Sans, sans-serif;
    font-size: 16px;
    font-weight: 300;
  }
}

.selectedQuestion .previewImg {
  border: 2px solid #b3b3b3;
  border-bottom: none;
}

